/* jetbrains-mono-300 - cyrillic_latin */
@font-face {
	font-display: swap;
	font-family: 'JetBrains Mono';
	font-style: normal;
	font-weight: 300;
	src: url('../fonts/jetbrains-mono/jetbrains-mono-v23-cyrillic_latin-300.woff2') format('woff2'); /* Chrome 36+, Opera 23+, Firefox 39+, Safari 12+, iOS 10+ */
}
